@import "https://fonts.googleapis.com/css2?family=Playwrite+IT+Moderna:wght@100..400&display=swap";
* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(#f2dfca, #fff) no-repeat;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Playwrite IT Moderna, cursive;
  font-weight: 400;
  display: flex;
}

main {
  flex: 1;
}

header {
  background-color: #f2dfca;
}

footer {
  background-color: #930101;
}

h1 {
  color: #930101;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  padding-top: 1em;
  display: flex;
}

h2 {
  text-align: center;
  color: #930101;
  display: block;
}

span.my-name {
  color: #fff;
  text-align: center;
  display: block;
}

span.contact-info {
  color: #930101;
  font-style: italic;
}

a.email-contact:link, a.email-contact:visited {
  color: #930101;
  text-decoration: none;
}

a.menu-link:link, a.menu-link:visited {
  color: #000;
  text-decoration: none;
}

a.menu-link:hover {
  color: #930101;
}

a.red-lined-link:link, a.red-lined-link:visited {
  color: #930101;
  text-decoration: none;
}

a.red-lined-link:hover {
  text-decoration: underline;
}

a.linked-image {
  text-align: center;
}

nav.menu-navigation {
  padding-bottom: 1em;
}

nav.menu-navigation ul.nav-ul {
  padding: 0;
  list-style-type: none;
}

img.image-white-gradient:hover {
  border: 5px solid #fff;
}

img.dishes-img {
  aspect-ratio: 1;
  border-top-left-radius: 5%;
  border-top-right-radius: 5%;
  width: 100%;
}

img.index-photo {
  margin: auto;
  display: block;
}

img.icons-food {
  width: 8%;
  height: auto;
}

div.dish-card {
  color: #fff;
  background-color: #930101;
  border-radius: 5%;
  margin-bottom: 1em;
}

div.dish-and-price {
  justify-content: space-between;
  display: flex;
}

div.container-card {
  text-align: left;
  aspect-ratio: 1;
  width: auto;
  height: auto;
  position: relative;
}

div.back-card {
  padding-left: 1em;
  padding-right: 1em;
  display: none;
}

div.main-history-container {
  flex-direction: column;
  margin-bottom: 1em;
  display: flex;
}

div.video-div {
  margin-bottom: 2em;
}

div.enlaces-div {
  font-family: Arial, Helvetica, sans-serif;
}

form {
  margin-bottom: 3em;
}

form label {
  margin-bottom: .3em;
  display: block;
}

form input {
  border: 1px solid #fff;
  width: 100%;
  height: 2em;
  display: block;
}

form textarea {
  resize: both;
  border: 1px solid #fff;
  width: 100%;
  height: 10em;
}

form small {
  color: red;
}

form small.confirmation-message {
  color: #000;
  margin-bottom: 0;
  padding-bottom: 0;
}

form button {
  color: #fff;
  float: inline-end;
  background-color: #930101;
  border: 1px solid #930101;
  border-radius: 10%;
  height: 2em;
}

figure {
  margin: 0;
  padding: 0;
  position: relative;
}

figure iframe {
  border: none;
  width: 100%;
}

figure figcaption {
  font-size: .7em;
}

@media screen and (width <= 399px) {
  main {
    padding-left: 10%;
    padding-right: 10%;
  }

  ul.nav-ul {
    text-align: center;
    display: block;
  }

  span.my-name {
    padding-top: 1em;
    padding-bottom: 1em;
    font-size: .5em;
  }

  img.index-photo {
    width: 80%;
  }

  img.places-img {
    width: 100%;
    height: auto;
  }

  div.menu-food-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  p.dish-text {
    padding-left: 1em;
    padding-right: 1em;
    font-size: .8em;
  }

  iframe {
    height: 47.6191vw;
  }
}

@media screen and (width >= 400px) and (width <= 799px) {
  main {
    padding-left: 10%;
    padding-right: 10%;
  }

  ul.nav-ul {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  ul.nav-ul li:not(:last-child):after {
    content: " | ";
    color: #930101;
    padding-right: 5px;
    font-weight: 900;
  }

  span.my-name {
    padding-top: 1em;
    padding-bottom: 1em;
    font-size: .7em;
  }

  img.index-photo {
    width: 70%;
  }

  img.places-img {
    object-fit: cover;
    width: 100%;
    height: auto;
  }

  div.menu-food-container {
    grid-template-columns: repeat(2, 1fr);
    display: grid;
  }

  div.dish-card {
    margin-left: 5%;
    margin-right: 5%;
  }

  p.dish-text {
    padding-left: 1em;
    padding-right: 1em;
    font-size: .8em;
  }

  p.history-paragraf {
    font-size: 1em;
  }

  iframe {
    height: 50vw;
  }
}

@media screen and (width >= 800px) {
  main {
    padding-left: 25%;
    padding-right: 25%;
  }

  span.my-name {
    padding-top: 2em;
    padding-bottom: 2em;
    font-size: 1em;
  }

  ul.nav-ul {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  ul.nav-ul li:not(:last-child):after {
    content: " | ";
    color: #930101;
    padding-right: 5px;
    font-weight: 900;
  }

  p.dish-text {
    padding-left: 1em;
    padding-right: 1em;
    font-size: 1em;
  }

  div.history-container {
    padding-bottom: 2em;
    display: flex;
  }

  div.history-container:nth-child(odd) {
    flex-direction: row;
  }

  div.history-container:nth-child(odd) p.history-paragraf {
    padding-right: 2em;
  }

  div.history-container:nth-child(2n) {
    flex-direction: row-reverse;
  }

  div.history-container:nth-child(2n) p.history-paragraf {
    padding-left: 2em;
  }

  div.history-container p.history-paragraf {
    font-size: 1rem;
  }

  img.places-img {
    object-fit: cover;
    width: 50%;
    height: auto;
    margin-top: 2em;
  }

  img.index-photo {
    width: 60%;
  }

  iframe {
    height: 28.5714vw;
  }

  div.main-container-contact-form {
    justify-content: space-between;
    display: flex;
  }

  div.contact-div {
    width: 100%;
    margin-left: 5%;
    margin-right: 5%;
  }

  div.menu-food-container {
    grid-template-columns: repeat(3, 1fr);
    display: grid;
  }

  div.dish-card {
    margin-left: 5%;
    margin-right: 5%;
  }

  div.enlaces-div {
    display: flex;
  }

  div.inner-enlaces-div {
    padding-left: 5%;
    padding-right: 5%;
  }

  button {
    margin-left: 0;
    margin-right: 5%;
  }
}
